
*{
    box-sizing: border-box;
    padding: 0;
    margin:0;
    outline: none;
}
.appear-animated{
    opacity: 0;
    animation: appear .4s ease forwards;
    @for $var from 1 to 20 {
        &:nth-child(#{$var}){
            animation-delay: $var * 20ms;
        }
    }
}


@keyframes appear{
    from{
        transform: scale(.9);
        opacity:.5;

    }
    to {
        transform: scale(1);
        opacity:1;
    }
}